import React, { useEffect, useState } from 'react';
import { getProxyURL } from '@/Tools/Proxy/utils.ts';

const GetProxyURL = () => {
    const [url, setUrl] = useState("");
    const [proxyURL, setProxyURL] = useState("");
    const [proxyReferer, setProxyReferer] = useState(""); //  proxy_referer 


    useEffect(() => {
        const httpsRegex = /https:\/\/[^\s/$.?#].[^\s]*/; // URL
        const updateURL = () => {
            const match = url.match(httpsRegex);
            let newProxyURL = getProxyURL(match ? match : "");

            //  proxyReferer proxyURL 
            if (proxyReferer) {
                const refererParam = encodeURIComponent(proxyReferer);
                newProxyURL += (newProxyURL.includes('?') ? '&' : '?') + `proxy_referer=${refererParam}`;
            }

            setProxyURL(newProxyURL); //  URL
        };

        updateURL();

        return;
    }, [url, proxyReferer])

    const handleDrop = (event) => {
        event.preventDefault(); // 
        const data = event.dataTransfer.getData('text'); // 
        setUrl(data);
    };

    const handleDragOver = (event) => {
        event.preventDefault(); //  drop 
    };
    
    const handleMouseEnter = (event) => {
        // 
        event.target.select();
    };

    const handlePaste = (event) => {
        event.preventDefault(); //  !important, paste...
        // const clipboardData = event.clipboardData || window.clipboardData; // 
        const clipboardData = event.clipboardData;
        const data = clipboardData.getData('Text'); // 
        setUrl(data);
    };

    // useEffect(() => {
    //     // 
    //     window.addEventListener('paste', handlePaste);
    //     return () => {
    //         // 
    //         window.removeEventListener('paste', handlePaste);
    //     };
    // }, []);

    return (
        <div className='h-full'
            onDrop={handleDrop} //  drop 
            onDragOver={handleDragOver} //  dragOver 
            // onPaste={handlePaste}
        >
            <input
                type="text"
                value={url}
                onChange={(e) => { setUrl(e.target.value) }} // 
                onMouseEnter={handleMouseEnter} // 
                placeholder=" URL,  Ctrl+V, "
                style={{ width: '100%', padding: '8px', marginBottom: '10px' }}
                // onPaste={handlePaste}
                // onPaste={()=>{}}
                // readOnly
            />
            <input
                type="text"
                value={proxyReferer}
                onChange={(e) => { setProxyReferer(e.target.value) }} //  proxy_referer 
                onMouseEnter={handleMouseEnter} // 
                placeholder=" referer URL"
                style={{ width: '100%', padding: '8px', marginBottom: '10px' }}
            />
            <input
                type="text"
                value={proxyURL}
                onMouseEnter={handleMouseEnter} // 
                readOnly
                placeholder="GPT"
                style={{ width: '100%', padding: '8px', marginBottom: '10px' }}
            />
        </div>
    );
};

export default GetProxyURL;