// "X-Host",
// "X-Origin",
// "X-Referer",
// Host
export const END_POINT = "proxy.moonchan.xyz"
// 
// hostport



export function fetchWithProxy(
  input: string | URL,
  init?: RequestInit,
): Promise<Response> {
  const url = new URL(input);
  const endpoint = "https://" + END_POINT + url.pathname + url.search

  if (!init) {
    return fetch(endpoint, {
      headers: {
        "X-Host": url.hostname,
      }
    });
  }

  if (!init.headers) {
    init.headers = {}; //  init.headers 
  }

  // debug

  //  init.headers  Headers 
  if (init.headers instanceof Headers) {
    init.headers.append("X-Host", url.hostname); //  append 
  } else if (Array.isArray(init.headers)) {
    //  push 
    init.headers.push(["X-Host", url.hostname]);
  } else {
    //  init.headers  Record<string, string>
    const headers = init.headers as Record<string, string>;
    headers["X-Host"] = url.hostname; //  X-Host 
  }

  return fetch(endpoint, init); //  init  fetch
}

export function getProxyURL(input: string) {
  console.log(input)
  if (input === "") return input;
  const url = new URL(input);
  url.searchParams.set('proxy_host', url.hostname); //  proxy_host 
  url.hostname = END_POINT;
  return url.toString();
}