import React, { useEffect, useState } from 'react';
import B23 from "./B23";
import BV from "./BV";
import Live from './Live';

const BiliCover = () => {
    const [url, setUrl] = useState("");
    const [b23id, setB23id] = useState("");
    const [bvid, setBvid] = useState("");
    const [liveid, setLiveid] = useState("");

    const extractIds = (url) => {
        const b23Regex = /https?:\/\/(?:www\.)?b23\.tv\/(\w+)/;
        const bvidRegex = /https?:\/\/(?:www\.)?bilibili\.com\/video\/(\w+)/;
        const liveRegex = /https?:\/\/live\.bilibili\.com\/(\w+)/;

        const b23Match = url.match(b23Regex);
        const bvidMatch = url.match(bvidRegex);
        const liveMatch = url.match(liveRegex);

        setB23id(b23Match ? b23Match[1] : "");
        setBvid(bvidMatch ? bvidMatch[1] : "");
        setLiveid(liveMatch ? liveMatch[1] : "");
    };

    const handleChange = (e) => {
        const newUrl = e.target.value; //  URL
        setUrl(newUrl); //  URL
        extractIds(newUrl); //  ID
    };

    const handlePaste = (event) => {
        // 
        event.preventDefault();
        // 
        const clipboardData = event.clipboardData || window.clipboardData;
        const pastedData = clipboardData.getData('Text'); // 

        setUrl(pastedData); //  URL
        extractIds(pastedData); //  ID
    };

    const handleMouseEnter = (event) => {
        // 
        event.target.select();
    };

    const handleDrop = (event) => {
        event.preventDefault(); // 
        const data = event.dataTransfer.getData('text'); // 
        setUrl(data); // 
        extractIds(data); //  ID
    };

    const handleDragOver = (event) => {
        event.preventDefault(); //  drop 
    };


    useEffect(() => {
        // 
        window.addEventListener('paste', handlePaste);
        
        // 
        return () => {
            window.removeEventListener('paste', handlePaste);
        };
    });

    return (
        <div className='h-full'
            onDrop={handleDrop} //  drop 
            onDragOver={handleDragOver} //  dragOver 
        >
            <input
                type="text"
                value={url}
                onChange={handleChange}
                onMouseEnter={handleMouseEnter} // 

                placeholder=" URL,  Ctrl+V, "
                style={{ width: '100%', padding: '8px', marginBottom: '10px' }}
            />
            {b23id && <B23 id={b23id} />}
            {bvid && <BV id={bvid} />}
            {liveid && <Live id={liveid} />}
            <div>():<a className="text-blue-500 hover:text-green-500 underline" href="https://bilicover.magecorn.com/help">https://bilicover.magecorn.com/help</a></div>
        </div>
    );
};

export default BiliCover;