import React, { useState } from 'react';
import { myFormat } from './utils.ts';


// import { reverseString } from '@/Tools/utils'
const InputWithUnderline = ({ value, placeholder }) => {
    // const [inputValue, setInputValue] = useState('');
    const [isCopied, setIsCopied] = useState(false); // 

    // const handleChange = (e) => {
    //     setInputValue(e.target.value);
    // };

    // // 24-11-17
    // const reverseString = (str) => {
    //     return str.split('').reverse().join(''); // 
    // };

    // const formatInputValue = (value) => {
    //     //  4 
    //     const reversed = reverseString(value);
    //     const formatted = reversed.replace(/(.{4})/g, '$1_'); // 4
    //     return reverseString(formatted).replace(/_$/, ''); // 
    // };

  
    const copyToClipboard = () => {
        const formattedValue = myFormat(value);
        navigator.clipboard.writeText(formattedValue)
            .then(() => {
                // 
                setIsCopied(true);
                setTimeout(() => {
                    setIsCopied(false);
                }, 200); // 1
            })
            .catch(err => {
                console.error(":", err);
            });
    };

    return (
        <input
            type="text"
            value={myFormat(value)}
            // onChange={handleChange}
            onClick={copyToClipboard}
            placeholder={placeholder}
            className={`border border-gray-300 p-2 rounded mb-2 w-full ${isCopied ? 'bg-green-200' : ''}`} // 
        />
    );
};

export default InputWithUnderline;